import { Hooks } from 'topwrite';
import lunr from 'lunr';

const hooks: Hooks = {
    async summary() {
        const { book: { summary }, tmpFS, logger } = this;
        logger.info('building search index ...');

        const documents: Array<{ ref: string, title: string, body: string }> = [];
        const articles = summary.getArticles();

        for (const article of articles) {
            if (article.ref) {
                documents.push({
                    ref: article.ref,
                    title: article.getTitle(),
                    body: await tmpFS.readAsString(article.ref)
                });
            }
        }

        require('lunr-languages/lunr.stemmer.support')(lunr);
        require('lunr-languages/lunr.multi')(lunr);
        require('lunr-languages/lunr.zh')(lunr);

        const idx = lunr(function() {
            // @ts-ignore
            this.use(lunr.multiLanguage('en', 'zh'));

            this.ref('ref');
            this.field('title');
            this.field('body');

            documents.forEach((doc) => {
                this.add(doc);
            });
        });

        await tmpFS.writeFile('search_index.json', JSON.stringify(idx));

        logger.info('build succeed');
    }
};

export default hooks;
